.pricing {
	overflow: hidden;
	background-color: #f7f8f9;
	padding: 78px 0 50px;
	&__header {
		@include theme_title; }
	&__text {
		@include theme_text; }
	&__header + &__text {
		max-width: 468px;
		margin: 13px auto 50px;
		text-align: center; }
	&__items {
		width: 100%;
		height: 100%;
		@include clr; }
	&__item {
		@include clr;
		width: 33%;
		float: left;
		margin: auto;
		text-align: center; }
	&__title {
		margin-bottom: 22px;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase; }
	&__head {
		position: relative;
		display: block;
		margin: 0 auto 14px;
		background-color: #fff;
		border-radius: 50%;
		width: 69px;
		height: 69px;
		&:after {
			content: "";
			@include vcenter; } }
	.standard {
		color: #afbe96;
		&:after {
			@include s(like); } }
	.popular {
		color: #627787;
		&:after {
			@include s(heart); } }
	.premium {
		color: #f3897f;
		&:after {
			@include s(star); } }
	&__value {
		color: #000000;
		font-size: 2.8125em;
		font-weight: 500;
		&:after {
			content: "per month";
			display: block;
			margin: 0 auto;
			width: 30%;
			color: #a4a4a4;
			font-size: 0.4em;
			font-weight: 400;
			padding-bottom: 30px;
			border-bottom: 1px solid #ddd; } }
	&__list {
		display: block;
		margin-top: 30px; }
	&__components {
		font-size: 13px;
		line-height: 30px;
		color: #a4a4a4;
		font-weight: 400;
		&.is-active {
			color: #404040;
			font-weight: 500; } }
	&__about {
		@include theme_text;
		font-size: 13px;
		max-width: 528px;
		margin: 92px auto 0;
		text-align: center; }
	&__button {
		display: block;
		margin: 40px auto 100px;
		background-color: #f6a79f;
		border-color: transparent;
		&:hover {
 } } }			// border: 3px solid #fdb352

@include r(801) {
	.pricing {
		padding: 28px 0 20px;
		&__about {
			margin: 22px auto 0; }
		&__button {
			margin: 20px auto 10px; } } }

@include r(766) {
	.pricing__value:after {
		width: 50%; } }

@include r(501) {
	.pricing__item {
		width: 100%;
		border-bottom: 1px solid #ddd;
		margin-bottom: 10px;
		padding-bottom: 5px;
		.pricing__block {
			float: left;
			width: 50%; }
		.pricing__list {
			float: right;
			width: 50%; }
		.pricing__value {
			font-size: 1.8em;
			&:after {
				width: 100%; } } }
	.pricing__header + .pricing__text {
		margin-bottom: 10px; } }
