.about-us {
	background-color: #f06c5f;
	color: #ffffff;
	text-align: center;
	// overflow: hidden
	&__head {
		position: relative;
		padding-top: 94px;
		font-size: 20px;
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 41px;
		&:before {
			content: "";
			@include vcenter;
			top: 18px;
			@include s(logo-m); } }
	&__title {
		font-size: 1.5625em;
		padding: 55px 90px;
		border-top: 3px solid #d65346;
		border-bottom: 3px solid #d65346; }
	&__info {
		@include clr;
		margin-top: 55px; }
	&__text {
		margin: 0 2.5%;
		max-width: 40.5%;
		float: left;
		font-size: 15px;
		text-align: left; }
	&__items {
		@include clr;
		width: 100%;
		font-size: 0;
		margin-bottom: 100px;
		margin-top: 63px; }
	.start {
		background-color: #de5a4d;
		span {
			color: #fff; } } }
.item {
	display: inline-block;
	width: 25%;
	min-width: 160px;
	margin: 10px 0;
	&__photo {
		border-radius: 50%;
		border: 5px solid #fff;
		box-sizing: border-box;
		max-width: 150px;
		max-height: 150px; }
	&__name {
		margin-top: 30px;
		font-size: 17px;
		font-weight: 500; }
	&__position {
		color: #ffbc64;
		font-size: 13px;
		margin-top: 2px;
		margin-bottom: 22px; }
	&__links {}
	&__link {
		margin: 0 2px;
		display: inline-block;
		&.linked-in {
			@include s(linked-in); }
		&.email {
			@include s(email); } } }

@include r(801) {
	.about-us {
		&__head {
			padding-top: 64px;
			margin-bottom: 21px; }
		&__title {
			padding: 25px; }
		&__info {
			margin-top: 15px; }
		&__text {
			max-width: 100%;
			float: none; }
		&__items {
			margin-bottom: 20px;
			margin-top: 13px; } } }
@include r(600) {
	.about-us__title {
		font-size: 1.2em; } }
