.customers {
	&__company {
		@extend .wrapper;
		@include clr;
		overflow: hidden; }
	&__button {
		display: block;
		margin: 97px auto 136px;
		background-color: #b4bdc5;
		border: none;
		font-size: 14px;
		font-weight: 700;
		line-height: 25px;
		&:hover {
 } }			// border: 3px solid #fdb352

	.company {
		position: relative;
		padding-top: 210px;
		float: left;
		max-width: 50%;
		text-align: center;
		&__logo {
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 253px; }
		&__img {
			max-width: 100%;
			max-height: 100%; }
		&__about {
			@include theme_text;
			max-width: 75%;
			margin: 0 auto; }
		&__ceo {
			position: relative;
			left: 0;
			right: 0;
			// width: 50%
			@include clr;
			margin-top: 43px;
			margin-left: 30%;
			text-align: left;
			.ceo__name {
				color: #000;
				padding-top: 15px;
				font-size: 17px;
				font-weight: 700; }
			.ceo__photo {
				max-width: 69px;
				min-height: 69px;
				border-radius: 50%;
				overflow: hidden;
				float: left;
				margin-right: 10px; }
			.ceo__comp {
				color: #a4a4a4;
				font-size: 11px;
				font-weight: 500;
				line-height: 18px; } } } }
.headline {
	background: url(../img/customers.jpg);
	@include cover;
	text-align: center;
	color: #ffffff;
	padding: 50px 0;
	width: 100%;
	height: 100%;
	&__title {
		margin-top: 39px;
		margin-bottom: 20px;
		font-size: 20px;
		font-weight: 700;
		text-transform: uppercase; }

	&__text {
		margin: 0 auto;
		max-width: 468px;
		font-size: 15px;
		margin-bottom: 30px; } }

@include r(801) {
	.headline {
		padding: 20px 0;
		&__title {
			margin-top: 15px;
			margin-bottom: 10px; }
		&__text {
			margin-bottom: 10px; } }
	.customers {
		&__company {
			font-size: 0; }
		&__button {
			margin: 17px auto 36px; }
		.company {
			font-size: 16px;
			float: none;
			display: inline-block;
			padding: 10px 0 20px;
			&__ceo {
				margin-top: 13px; } }
		.company__logo {
				position: relative;
				bottom: 0; } } }
@include r(595) {
	.customers {
		.company {
			max-width: 100%;
			&__ceo {
				margin-left: 20%; } } } }
