.line {
	position: relative;
	height: 3px;
	width: 100%;
	background-color: #777;
	opacity: 0.2;
	margin-bottom: 40px;
	.dot {
		display: block;
		position: absolute;
		top: -14px;
		left: 0;
		width: 27px;
		height: 27px;
		border: 3px solid #f7f8f9;
		border-radius: 50%;
		background-color: #f06c5f;
		&:hover {
			cursor: pointer;
			box-shadow: 0 0 0 5px rgba(255, 153, 51, 0.6); }
		&.is-active {
			box-shadow: 0 0 0 10px rgba(135, 153, 163, 0.6); } } }
