.features {
	position: relative;
	margin-top: 77px;
	border-bottom: 1px solid #ddd;
	&__header {
		@include theme_title; }
	&__text {
		@include theme_text; }
	&__header + &__text {
		max-width: 468px;
		margin: 13px auto 0;
		text-align: center; }
	&__inner {
		@include clr;
		border-top: 1px solid transparent;
		background-position: right center;
		background-repeat: no-repeat;
		background-size: 50%;
		&:not(:last-child) {
			border-bottom: 1px solid #ddd; }
		&:nth-child(2n) {
			background-position: left center;
			.features__block {
				float: right; } }
		&.bg-1 {
			background-image: url(../img/smart-tds.png); }
		&.bg-2 {
			background-image: url(../img/cpi-cpc.png); }
		&.bg-3 {
			background-image: url(../img/adrotator.png); }
		&.bg-4 {
			background-image: url(../img/source-tracking.png); }
		&.bg-5 {
			background-image: url(../img/offer-checker.png); } }
	&__block {
		max-width: 34%;
		margin-top: 140px;
		margin-bottom: 120px; }
	&__title {
		color: #627787;
		font-size: 17px;
		font-weight: 500;
		text-transform: uppercase;
		margin-bottom: 40px; }
	&__link {
		display: block;
		margin-top: 34px;
		color: #f06c5f;
		font-size: 0.875em;
		font-weight: 500;
		text-transform: uppercase; } }
@include r(801) {
	.features__block {
		max-width: 40%;
		margin-top: 40px;
		margin-bottom: 40px; }
	.features__header + .features__text {
		max-width: 90%; } }

@include r(500) {
	.features {
		margin-top: 17px;
		&__block {
			max-width: 44%;
			margin-top: 20px;
			margin-bottom: 20px; }
		&__title {
			margin-bottom: 10px; }
		&__link {
			margin-top: 14px; } } }
@include r(425) {
	.features {
		&__block {
			max-width: 60%; }
		&__inner {
			background-size: 40%; } } }
@include r(400) {
	.features {
		&__block {
			max-width: 100%; }

		&__inner {
			margin-top: 10px;
			padding-top: 110px;
			background-size: 70%;
			background-position: center top;
			&:nth-child(2n) {
				background-position: center top; }
			&.bg-1 .features__block {
				margin-top: 0; } } } }
